html {
    height: 100%;
}
body {
    height: 100%;
    margin: 0;

    background: #3f47a1; /* Old browsers */
    background: -moz-linear-gradient(45deg, #3f47a1 0%, #113b6d 100%); /* FF3.6-15 */
    background: -webkit-linear-gradient(45deg, #3f47a1 0%,#113b6d 100%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(45deg, #3f47a1 0%,#113b6d 100%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#3f47a1', endColorstr='#113b6d',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */

    background-repeat: no-repeat;
    background-attachment: fixed;
}

.question_wrap {
  padding: 100px;
}

.question-number {
  width: 100%;
  font-size: 30px;
  color: rgba(255, 255, 255, 0.5);
  font-family: 'Pragati Narrow', sans-serif;
  border-bottom: 6px solid rgba(255, 255, 255, 0.5);
  display: inline-block;
  margin-bottom: 30px;
  padding: 20px 0;
  text-align: center;
}

.large-text {
  font-size: 60px;
  line-height: 70px;
  color: #ffffff;
  font-family: 'Oswald', sans-serif;
}

.center-text {
  text-align: center;
}

.clockface {
  font-size: 120px;
  text-align: center;
  margin-top: 200px;
}

.slide_wrap {
  padding-left: 0;
  list-style: none;
  position: relative;
}

.slide {
  position: absolute;
  left: 100%;
  width: 100%;
}

.slide.active {
  left: 0;
}



